body {
  margin: 0;
  padding: 0;
  
  overflow: hidden;
}

.App {
  text-align: center;
}

#root{
  font-family: 'Jura', sans-serif;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.selectedItem {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-filter: drop-shadow( 0 0 10px #11ffea);
            filter: drop-shadow( 0 0 10px #11ffea);
}

.selectableWrapper{
    position: relative;
    display: inline-block;
    margin-left:10px;
    transition: all 0.2s ease-in-out;
    margin:0;
}

.selectableWrapper img{
    height:43vh;
    border-radius: 1%;
}

.favorited {
    position: absolute;
    top:0.2vw;
    right:0.4vw;
    text-align: center;
    color:red;
}



div.scrollmenu {
  background-color: transparent;
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
  height:52vh;
  clear: both;
}
.SliderMenu h3 {
  padding-top:2vh;
  padding-bottom:0;
  color:white;
} 

.SliderMenu{
  height:57vh;
} 


  
.infoContainer{
    height: 90vh;
    padding: 2vh;
}

.movieTextInfo{
    color:white;
}

.movieTextInfo h3{
    font-size: 4vh;
}

.movieTextInfo h1{
    font-size: 6vh;
    font-weight: 500;
}

.movieTextInfo p li{
    font-size: 2vh;
}

.faved{
    font-size: 3vh;
    color:red;
}

.moviePictureWrapper{
    -webkit-filter: drop-shadow( 0 0 10px white);
            filter: drop-shadow( 0 0 10px white);
    max-width: 100%;
    max-height: 100%;
    border: white 1px solid;
}
#root{
    background-color: black;
}


